import React, { Component } from "react";

class index extends Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    console.log(this.props.matchDetails);
  }
  render() {
    if (!this.props.matchDetails) return <div>Loading...</div>;
    return (
      <h2 className="team-title">{`.::${this.props.matchDetails[0].batting_team} - VS - ${this.props.matchDetails[0].bowling_team}::.`}</h2>
    );
  }
}

export default index;
